.parent {
    background:
        url('../resources/home/homeImage.png') no-repeat;
    position: relative;
    border-radius: 0 0 2rem 2rem;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: 0rem;
}

.banner {
    background: linear-gradient(rgba(37, 36, 36, 0.5), rgba(34, 34, 34, 0.5)), url('../resources/home/writing-image.svg');
    border-radius: 1rem;
    margin-top: 2rem;
}

@media only screen and (max-width: 768px){
    .parent{
        background-position: left center;
    }
}
